import React, { useEffect, useRef } from "react";
import '../style/Project2.css'

function Project2({ onClose }) {
  const imgScrollRef = useRef(null);

  // 자동 세로 스크롤 효과
  useEffect(() => {
    const el = imgScrollRef.current;
    if (!el) return;
    let pos = 0;
    let dir = 1;
    let raf;
    const speed = 0.7;
    const step = () => {
      const max = el.scrollHeight - el.clientHeight;
      pos += dir * speed;
      if (pos >= max) {
        pos = max;
        dir = -1;
      } else if (pos <= 0) {
        pos = 0;
        dir = 1;
      }
      el.scrollTop = pos;
      raf = requestAnimationFrame(step);
    };
    raf = requestAnimationFrame(step);
    return () => cancelAnimationFrame(raf);
  }, []);

  return (
    <section className="pj1-section game-shadow-bg">
      <button className="pj1-back-btn" onClick={onClose}>
        BACK !
      </button>
      <div className="pj1-inner game-border">
        <div className="pj1-left">
          <div className="pj1-img-frame pj1-tone-down game-glow">
            <div className="pj1-img-scroll" ref={imgScrollRef}>
              {/* 이미지 파일만 맞게 교체하면 됨 */}
              <img
                src={process.env.PUBLIC_URL + "/wehipfull.png"}
                alt="프로젝트2 전체 미리보기"
                className="pj1-img"
              />
            </div>
          </div>
        </div>
        <div className="pj1-right">
          <div className="pj1-label">Responsive Web project</div>
          <div className="pj1-title">WEHip</div>
          <span className="pj1-grad">반응형 웹 사이트</span>
          <div className="pj1-desc">
            평소에 힙합 관련 홈페이지를 원했던 제 자신이 <br />
            페르소나가 되어 만들었던 반응형 웹 사이트 WEHip 입니다. <br />
            메인페이지 와 서브페이지 모두 3단계로 나눠 반응형 구조를 구현했습니다.
          </div>
          <div className="pj1-tags">
            <span>#HTML5</span>
            <span>#CSS3</span>
            <span>#Media Query</span>
          </div>
          <div className="pj1-btns">
            <a
              href="https://yhw1130y.github.io/project002/"
              target="_blank"
              rel="noopener noreferrer"
              className="pj1-btn game-btn"
            >
              Demo
            </a>
            <a
              href="https://yhw1130y.github.io/project002/wehip.pdf"
              target="_blank"
              rel="noopener noreferrer"
              className="pj1-btn game-btn"
            >
              Report
            </a>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Project2;
